<template>
<CardBox has-table>
<div>
<table>
<thead class="text-xs text-gray-700 uppercase dark:text-gray-400">
<tr>
  <th></th>
  <th>Project</th>
  <th><b>{{ metricLabel }}</b></th>
  <th>Price</th>
  <th>Marketcap</th>
</tr>
</thead>
<tbody>
  <tr v-for="alert in alertsPaginated" :key="alert.id">
    <th data-label="Logo" class="w-6 h-6">
        <div class="h-6 w-6">                        
          <img :src="alert.logo ? alert.logo : 'https://cdn4.iconfinder.com/data/icons/leto-weather/64/na_not_available_weather_sun-512.png'" alt="Logo" class="rounded-full flex justify-center align-center content-center">
        </div>
      </th>
          <td data-label="Name" class="w-3/12">
            <small class="text-gray-500 dark:text-slate-400 text-right">   
              <router-link :to="`/projects/${alert.symbol}`">
              {{ (alert.name)}}
            </router-link>
            </small>
          </td>
          <td data-label="Date set up" class="lg:w-1 whitespace-nowrap">
            <small class="text-gray-500 dark:text-slate-400"> 
              <p v-html="formatValue(alert[this.$props.metric], this.$props.metric)"></p>

            </small>
          </td>
          <td data-label="Rule" class="lg:w-1 whitespace-nowrap">
            <small class="text-gray-500 dark:text-slate-400"> 
                {{ formatValue(alert.price, 'price') }}

            </small>
          </td>

          <td data-label="Active" class="lg:w-1 whitespace-nowrap">
            <small class="text-gray-500 dark:text-slate-400"> 
              {{ formatValue(alert.marketcap, 'price') }}

            </small>
          </td>

</tr>
</tbody>
<tfoot class="text-xs text-gray-700 uppercase dark:text-gray-400">
<tr>
  <th></th>
  <th>Project</th>
  <th><b>{{ metricLabel }}</b></th>
  <th>Price</th>
  <th>Marketcap</th>

</tr>
</tfoot>
</table>
<!--PAGINATION--> 
<div class="p-3 lg:px-6 border-t border-gray-100 dark:border-slate-800">
  <BaseLevel>
    <BaseButtons>
      <BaseButton class="text-xs text-gray-700 uppercase dark:text-gray-400" label="Previous" @click="currentPage = Math.max(currentPage - 1, 0)" :color="page === currentPage ? 'lightDark' : 'whiteDark'"/>
        <BaseButton
          class="text-xs text-gray-700 uppercase dark:text-gray-400"
          v-for="page in pagesList.slice(Math.max(currentPage - 1, 0), Math.min(currentPage + 1, numPages))"
          :key="page"
          :active="page === currentPage"
          :label="page + 1"
          :color="page === currentPage ? 'lightDark' : 'whiteDark'"
          small
          @click="currentPage = page"
        />
        <BaseButton class="text-xs text-gray-700 uppercase dark:text-gray-400" label="Next" @click="currentPage = Math.min(currentPage + 1, numPages - 1)" :color="page === currentPage ? 'lightDark' : 'whiteDark'"/>
  </BaseButtons>

    <small>Page {{ currentPageHuman }} of {{ numPages }}</small>
  </BaseLevel>
</div>
</div>
</CardBox>
</template>

<script>
import BaseLevel from "@/components/BaseLevel.vue";
import BaseButtons from "@/components/BaseButtons.vue";
import BaseButton from "@/components/BaseButton.vue";
import UserAvatar from "@/components/UserAvatar.vue";
import CardBoxComponentTitle from "@/components/CardBoxComponentTitle.vue";
import CardBox from "@/components/CardBox.vue";
import { formatValue } from "@/stores/supastore";
import { metricOptions } from "@/stores/metricOptions";

export default {
components: {
  CardBox,
  CardBoxComponentTitle,
  BaseLevel,
  BaseButtons,
  BaseButton,
  UserAvatar,
},
props: {
    assets: {
      type: Array,
      required: true
    },
    metric: {
      type: String,
      required: true
    }
  },
data() {
return {
    myAlerts: this.assets,
    metric : this.metric,
    metricOptions: metricOptions,
    errors: null,
    user: {},
    alertsPaginated: [],
    currentPage: 0,
    perPage: 6,
    loaded: false
      };
},
  async created () {
    await this.updateAlertsPaginated();
  },
  watch: {
    currentPage() {
    this.alertsPaginated = this.myAlerts.slice(
      this.perPage * this.currentPage,
      this.perPage * (this.currentPage + 1)
    );
    }
  },
computed: {
    numPages() {
    if (this.myAlerts.length === 0) {
      return 0;
    }
    return Math.ceil(this.myAlerts.length / this.perPage);
    },
    currentPageHuman() {
    return this.currentPage + 1;
    },
    pagesList() {
    const pagesList = [];
    for (let i = 0; i < this.numPages; i++) {
      pagesList.push(i);
    }
    return pagesList;
    },
    metricLabel() {
      const metricOption = this.metricOptions.find(option => option.id === this.metric);
      return metricOption ? metricOption.label : '';
    }
}, 
methods: {
    updateAlertsPaginated() {
      const start = this.perPage * this.currentPage;
      const end = start + this.perPage;
      this.alertsPaginated = this.myAlerts.slice(start, end);
    },
    formatValue(coin, metric) {
      return formatValue(coin, metric)
    },
},
};
</script>

<style>
.clickable:hover {
cursor: pointer;
}
</style>