<template>
    <SuccessError v-if="success" type="success" message="Coins filtered bro - check it out below: " />
    <div v-if="success" class="py-8">
        <FilterTable :assets="assets" :metric="selectedOption" />
    </div> 
    <CardBox v-else>
            <form @submit.prevent="submitForm">     
                    <div class="w-full py-4 flex justify-center text-center inline-flex items-center">

                      <select v-model="selectedOption" class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500">
                          <option v-for="option in options" v-bind:value="option.id" required>{{ option.label }}</option>
                      </select>
                      <select v-model="selectedComparator" class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500">

                          <option v-for="option in comparatorOptions" v-bind:value="option" required>{{ option }}</option>
                      </select>
                      <input type="number" v-model="numericalValue" class=" bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" required >
                    </div>
                <!--SUBMIT-->
                <div class="flex justify-center">
                        <button type="submit" class="text-gray-900 bg-white hover:bg-gray-100 border border-gray-200 focus:ring-4 focus:outline-none focus:ring-gray-100 font-medium rounded-lg text-sm px-5 py-2.5 text-center inline-flex items-center dark:focus:ring-gray-600 dark:bg-gray-800 dark:border-gray-700 dark:text-white dark:hover:bg-gray-700 mr-2 mb-2">
                         <img class="w-4 h-4 mr-2" src="https://cdn3.iconfinder.com/data/icons/simple-web-navigation/165/tick-512.png" alt="plus">
                          Filter coins
                        </button>
                </div>
            </form>
    </CardBox>
</template>

<script>
import CardBox from "@/components/CardBox.vue";
import FilterTable from '@/components/FilterTable.vue';
import { supabase } from "../supabase";
import { metricOptions } from "@/stores/metricOptions";
import SuccessError from "@/components/SuccessError.vue";


export default {
  components: { 
    CardBox,
    FilterTable,
    SuccessError
},
  data() {
    return {
      success: false,
      error: null,
      assets: {},
      selectedOption: "price",
      options: metricOptions,
      selectedComparator: ">",
      comparatorOptions: ["=", "<", ">"],
      numericalValue: 0
    };
  },
  methods: {
    async submitForm() {
        await this.fetchFilteredAssetData();
        this.success = true;  
    },
    async fetchFilteredAssetData() {
    try {
        // Fetch the asset data for all coins from the Supabase DB
        let { data: jsoncoin, errors } = await supabase
        .from('jsoncoin')
        .select('payload')
        .order('id', { ascending: false });

        if (errors) return console.error("result" + errors);

        //Filter the dataset based on user's input
        let filterCriteria = this.numericalValue;
        if (this.selectedComparator === ">") {
            filterCriteria = item => item[this.selectedOption] > this.numericalValue;
        } else if (this.selectedComparator === "<") {
            filterCriteria = item => item[this.selectedOption] < this.numericalValue;
        } else {
            filterCriteria = item => item[this.selectedOption] === this.numericalValue;
        }
      
        let filteredDataset = jsoncoin[0]['payload'].filter(filterCriteria);
        // Sort the filtered dataset by the selected metric
        filteredDataset.sort((a, b) => {
            return b[this.selectedOption] - a[this.selectedOption];
        });
        
        // Get logos and add them to the fitlered dataset
        let { data: logos, error } = await supabase
         .from('ultimateframe')
          .select('logo, symbol');

        // Update the assets object
        this.assets = filteredDataset.map(asset => {
        let logo = logos.find(logo => logo.symbol === asset.symbol);
        if(logo) {
          asset.logo = logo.logo;
        } else {
          asset.logo = "https://cdn4.iconfinder.com/data/icons/leto-weather/64/na_not_available_weather_sun-512.png";
        }
        return asset;
          });

    } catch (errors) {
        console.error("Following errors were found: ", errors);
        this.error = errors.message;
    }
  },
  },
};
</script>
